export default {
	data () {
		return {
			
		}
	},
	methods: {
		fnCreatBarcode () {
			const currentWebview = this.$mp.page.$getAppWebview();
			
			var barcode = plus.barcode.create('barcode', [plus.barcode.QR], {  
			    top:'140px',  
			    left:'0px',  
			    width: '100%',  
			    height: '300px',  
			    position: 'static'  
			});  
			//此处未演示扫码成功回调的地址设置，实际请参考HTML5Plus API自行处理  
			//注意扫码区域需为正方形，否则影响扫码识别率  
			currentWebview.append(barcode);
			
			barcode.start({
				conserve: true, //是否保存扫码捷途，通过onmarked回调函数返回图片路径
				filename: '',	//保存扫码成功时图片保存路径
				vibrate: true,	//扫码成功时是否需要震动提醒
				sound: 'default'	//扫码成功时播放的提示音
			});
			
			barcode.onmarked = function (type, code, file) {
				console.log(type, code, file, '2');
				plus.runtime.openURL(code);
			}
			
			barcode.onerror = function (error) {
				console.log(error, '2');
			}
		},
		fnCreatBarcodeApi () {
			uni.scanCode({
			    success: function (res) {
					plus.runtime.openURL(res.result);
			        console.log('条码内容：' + res.result);
			        console.log('条码类型：' + res.scanType);
			        console.log('条字符集：' + res.charSet);
			        console.log('合法是返回：' + res.path);
			    }
			});
		}
	}
}
